import {
	myRequestGet
} from '@/utils/zgrequest.js'
import {
	ImageCategoryUrl,
	ImagesUrl
} from '@/utils/api.js'

export default {
	data() {
		return {
			//左边分类数据
			cates: [],
			//当前激活的分类
			active: 3,
			//分类详情数据
			secondData: []
		}
	},
	onLoad() {
		this.getPicsCate()
	},
	methods: {
		//获取分类数据
		async getPicsCate() {
			const result = await myRequestGet(ImageCategoryUrl)
			this.cates = result.message
			this.leftClickHandle(this.active, this.cates[this.active])
		},
		async leftClickHandle(index, item) {
			this.active = index;
			const res = await myRequestGet(ImagesUrl + item.id)
			this.secondData = res.message
		},
		//图片预览
		previewImg(current) {
			const urls = this.secondData.map(item => {
				return item.img_url
			})
			uni.previewImage({
				current,
				urls
			})
		}
	}
}
